import TeamCard from "../../components/teamCard/TeamCard";
import React, {useContext} from "react";
import {Fade} from "react-reveal";

import StyleContext from "../../contexts/StyleContext";
import {teamSection} from "../../portfolio";

import "../team/Team.scss";

export default function Team() {
  const {isDark} = useContext(StyleContext);
  if (!teamSection || !teamSection.display) return null;

  return (
    <Fade bottom duration={1000} distance="20px">
      <div className="main" id="team">
        <div className="team-main-div">
          <div className="team-header">
            <h1
              className={
                isDark
                  ? "dark-mode heading team-heading"
                  : "heading team-heading"
              }
            >
              {teamSection.title}
            </h1>
            <p
              className={
                isDark
                  ? "dark-mode subTitle team-subtitle"
                  : "subTitle team-subtitle"
              }
            >
              {teamSection.subtitle}
            </p>
          </div>

          <div className="team-category">
            <h2
              className={
                isDark
                  ? "dark-mode heading team-subheading"
                  : "heading team-subheading"
              }
            >
              Faculty
            </h2>
            <div className="team-cards-div">
              {teamSection.faculty.map((card, i) => (
                <TeamCard
                  key={i}
                  isDark={isDark}
                  cardInfo={{
                    title: card.title,
                    description: card.subtitle,
                    image: card.image,
                    imageAlt: card.imageAlt,
                    footer: card.footerLink
                  }}
                />
              ))}
            </div>
          </div>

          <div className="team-category">
            <h2
              className={
                isDark
                  ? "dark-mode heading team-subheading"
                  : "heading team-subheading"
              }
            >
              Students
            </h2>
            <div className="team-cards-div">
              {teamSection.students.map((card, i) => (
                <TeamCard
                  key={i}
                  isDark={isDark}
                  cardInfo={{
                    title: card.title,
                    description: card.subtitle,
                    image: card.image,
                    imageAlt: card.imageAlt,
                    footer: card.footerLink
                  }}
                />
              ))}
            </div>
          </div>

          <div className="team-category">
            <h2
              className={
                isDark
                  ? "dark-mode heading team-subheading"
                  : "heading team-subheading"
              }
            >
              Alumni
            </h2>
            <div className="team-cards-div">
              {teamSection.alumni.map((card, i) => (
                <TeamCard
                  key={i}
                  isDark={isDark}
                  cardInfo={{
                    title: card.title,
                    description: card.subtitle,
                    image: card.image,
                    imageAlt: card.imageAlt,
                    footer: card.footerLink
                  }}
                />
              ))}
            </div>
          </div>
        </div>
      </div>
    </Fade>
  );
}
